	module.exports = {"data":{"post":{"html":"<p>We've returned from our getaway on Tegernsee, which seems to be the Munich equivalent of the Alster, with the same high real estate prices, except that it's outside the city, not right in it. Very poor traffic planning, there were always traffic jams. Lots of private clinics though. It must be the dream of every German doctor to open a private clinic on Tegernsee.</p>\n<p>This is the first time I've spent any time in Bavaria (outside of Munich, which according to the Munichers I know is not part of Bavaria at all, despite being the Bavarian capital). Maybe it's just the north German prejudices that I've picked up, but it didn't feel very German at all. It felt more like Austria.</p>\n<p>I'll quit now that I've insulted all of the German-speaking regions of Europe (including the Swiss, by not mentioning them).</p>","fields":{"title":"Back In Germany","date":"18 Sep 2004","url":"https://papascott.de/2004-09-18-back-in-germany/"}}},"pathContext":{"slug":"/2004-09-18-back-in-germany/","prev":{"fields":{"title":"The Weekend Is Now","date":"16 Sep 2004","slug":"/2004-09-16-the-weekend-is-now/"}},"next":{"fields":{"title":"Brown Nosing","date":"19 Sep 2004","slug":"/2004-09-19-brown-nosing/"}}}}

